import React, { useState, useEffect } from 'react';
import { Container, Row, Col, Card, Button, Badge, Modal, Form, Alert, Tab, Tabs } from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { 
  faMapMarkerAlt, faEdit, faTrashAlt, faPlus, faSearch, faFilter, 
  faCheckCircle, faTimesCircle, faBan 
} from '@fortawesome/free-solid-svg-icons';
import { toast } from 'react-toastify';
import axios from 'axios';

const ManageLots = () => {
  const [lots, setLots] = useState([]);
  const [filteredLots, setFilteredLots] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [showAddModal, setShowAddModal] = useState(false);
  const [showEditModal, setShowEditModal] = useState(false);
  const [showBulkModal, setShowBulkModal] = useState(false);
  const [selectedLot, setSelectedLot] = useState(null);
  const [searchTerm, setSearchTerm] = useState('');
  const [statusFilter, setStatusFilter] = useState('all');
  const [activeTab, setActiveTab] = useState('all');
  
  // Form states
  const [formData, setFormData] = useState({
    lotNumber: '',
    location: '',
    size: '',
    price: 0,
    status: 'available'
  });
  
  // Bulk generation states
  const [bulkData, setBulkData] = useState({
    prefix: '',
    start: 1,
    end: 10,
    location: '',
    size: '',
    price: 0
  });

  useEffect(() => {
    fetchLots();
  }, []);

  useEffect(() => {
    // Apply filters whenever search term or status filter changes
    filterLots();
  }, [searchTerm, statusFilter, lots]);

  const fetchLots = async () => {
    try {
      const res = await axios.get('/api/lots');
      setLots(res.data);
      setFilteredLots(res.data);
      setLoading(false);
    } catch (err) {
      setError('Failed to load lots. Please try again later.');
      console.error('Error fetching lots:', err);
      setLoading(false);
    }
  };

  const filterLots = () => {
    let filtered = [...lots];
    
    // Apply status filter
    if (statusFilter !== 'all') {
      filtered = filtered.filter(lot => lot.status === statusFilter);
    }
    
    // Apply search term
    if (searchTerm.trim() !== '') {
      const search = searchTerm.toLowerCase();
      filtered = filtered.filter(lot => 
        (lot.lotNumber && lot.lotNumber.toLowerCase().includes(search)) ||
        (lot.location && lot.location.toLowerCase().includes(search))
      );
    }
    
    setFilteredLots(filtered);
  };

  const handleTabChange = (tab) => {
    setActiveTab(tab);
    
    if (tab === 'all') {
      setStatusFilter('all');
    } else if (tab === 'available') {
      setStatusFilter('available');
    } else if (tab === 'booked') {
      setStatusFilter('booked');
    } else if (tab === 'unavailable') {
      setStatusFilter('unavailable');
    }
  };

  const resetForm = () => {
    setFormData({
      lotNumber: '',
      location: '',
      size: '',
      price: 0,
      status: 'available'
    });
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const handleBulkInputChange = (e) => {
    const { name, value } = e.target;
    setBulkData({ ...bulkData, [name]: value });
  };

  const showAdd = () => {
    resetForm();
    setShowAddModal(true);
  };

  const showEdit = (lot) => {
    setSelectedLot(lot);
    setFormData({
      lotNumber: lot.lotNumber,
      location: lot.location || '',
      size: lot.size || '',
      price: lot.price || 0,
      status: lot.status
    });
    setShowEditModal(true);
  };

  const showBulkAdd = () => {
    setBulkData({
      prefix: '',
      start: 1,
      end: 10,
      location: '',
      size: '',
      price: 0
    });
    setShowBulkModal(true);
  };

  const handleAddLot = async () => {
    try {
      const res = await axios.post('/api/lots', formData);
      setLots([...lots, res.data]);
      setShowAddModal(false);
      resetForm();
      toast.success('Lot created successfully!');
    } catch (err) {
      toast.error(
        err.response && err.response.data.message
          ? err.response.data.message
          : 'Failed to create lot'
      );
    }
  };

  const handleEditLot = async () => {
    if (!selectedLot) return;

    try {
      const res = await axios.put(`/api/lots/${selectedLot.id}`, formData);
      
      // Update lot in state
      const updatedLots = lots.map(lot => 
        lot.id === selectedLot.id ? res.data : lot
      );
      
      setLots(updatedLots);
      setShowEditModal(false);
      toast.success(`Lot ${res.data.lotNumber} updated successfully!`);
    } catch (err) {
      toast.error(
        err.response && err.response.data.message
          ? err.response.data.message
          : 'Failed to update lot'
      );
    }
  };

  const handleBulkGenerate = async () => {
    try {
      const res = await axios.post('/api/lots/generate', bulkData);
      
      // Refresh lots to show new ones
      await fetchLots();
      
      setShowBulkModal(false);
      toast.success(`Generated ${res.data.totalCreated} lots successfully!`);
      
      if (res.data.errors && res.data.errors.length > 0) {
        toast.warning(`${res.data.errors.length} lots could not be created (already exist).`);
      }
    } catch (err) {
      toast.error(
        err.response && err.response.data.message
          ? err.response.data.message
          : 'Failed to generate lots'
      );
    }
  };

  const handleDeleteLot = async (id) => {
    if (!window.confirm('Are you sure you want to delete this lot?')) return;

    try {
      await axios.delete(`/api/lots/${id}`);
      
      // Remove lot from state
      const updatedLots = lots.filter(lot => lot.id !== id);
      setLots(updatedLots);
      
      toast.success('Lot deleted successfully!');
    } catch (err) {
      toast.error(
        err.response && err.response.data.message
          ? err.response.data.message
          : 'Failed to delete lot'
      );
    }
  };

  const getStatusBadge = (status) => {
    let variant, icon;
    switch (status) {
      case 'available':
        variant = 'success';
        icon = faCheckCircle;
        break;
      case 'booked':
        variant = 'primary';
        icon = faMapMarkerAlt;
        break;
      default:
        variant = 'danger';
        icon = faBan;
    }
    return (
      <Badge bg={variant}>
        <FontAwesomeIcon icon={icon} className="me-1" />
        {status.charAt(0).toUpperCase() + status.slice(1)}
      </Badge>
    );
  };

  if (loading) {
    return (
      <Container className="py-5 text-center">
        <div className="spinner-border text-primary" role="status">
          <span className="visually-hidden">Loading...</span>
        </div>
        <p className="mt-3">Loading lots...</p>
      </Container>
    );
  }

  if (error) {
    return (
      <Container className="py-5">
        <Alert variant="danger">{error}</Alert>
      </Container>
    );
  }

  return (
    <Container className="py-5">
      <div className="d-flex justify-content-between align-items-center mb-4">
        <h2>
          <FontAwesomeIcon icon={faMapMarkerAlt} className="me-2" />
          Manage Lots
        </h2>
        <div>
          <Button variant="success" className="me-2" onClick={showAdd}>
            <FontAwesomeIcon icon={faPlus} className="me-1" />
            Add Lot
          </Button>
          <Button variant="primary" onClick={showBulkAdd}>
            <FontAwesomeIcon icon={faPlus} className="me-1" />
            Bulk Generate
          </Button>
        </div>
      </div>
      
      {/* Search and Filter */}
      <Row className="mb-4">
        <Col md={6} className="mb-3 mb-md-0">
          <div className="input-group">
            <span className="input-group-text">
              <FontAwesomeIcon icon={faSearch} />
            </span>
            <Form.Control
              type="text"
              placeholder="Search by lot number or location..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
            />
          </div>
        </Col>
        <Col md={6} className="d-flex justify-content-md-end">
          <Form.Select 
            value={statusFilter}
            onChange={(e) => setStatusFilter(e.target.value)}
            className="w-auto"
          >
            <option value="all">All Statuses</option>
            <option value="available">Available</option>
            <option value="booked">Booked</option>
            <option value="unavailable">Unavailable</option>
          </Form.Select>
        </Col>
      </Row>
      
      {/* Status Tabs */}
      <Tabs
        activeKey={activeTab}
        onSelect={handleTabChange}
        className="mb-4"
      >
        <Tab eventKey="all" title="All Lots" />
        <Tab 
          eventKey="available" 
          title={<span>Available <Badge bg="success">{lots.filter(l => l.status === 'available').length}</Badge></span>}
        />
        <Tab eventKey="booked" title="Booked" />
        <Tab eventKey="unavailable" title="Unavailable" />
      </Tabs>
      
      {/* Lots List */}
      {filteredLots.length === 0 ? (
        <Alert variant="info">
          No lots found matching your filters.
        </Alert>
      ) : (
        <Row>
          {filteredLots.map(lot => (
            <Col md={4} lg={3} className="mb-4" key={lot.id}>
              <Card className="h-100 shadow-sm">
                <Card.Header className="d-flex justify-content-between align-items-center">
                  <h5 className="mb-0">Lot {lot.lotNumber}</h5>
                  {getStatusBadge(lot.status)}
                </Card.Header>
                <Card.Body>
                  {lot.location && (
                    <Card.Text>
                      <strong>Location:</strong> {lot.location}
                    </Card.Text>
                  )}
                  {lot.size && (
                    <Card.Text>
                      <strong>Size:</strong> {lot.size}
                    </Card.Text>
                  )}
                  <Card.Text>
                    <strong>Price:</strong> ${Number(lot.price).toFixed(2)}
                  </Card.Text>
                  <div className="d-flex justify-content-between mt-3">
                    <Button 
                      variant="outline-primary" 
                      size="sm"
                      onClick={() => showEdit(lot)}
                    >
                      <FontAwesomeIcon icon={faEdit} className="me-1" />
                      Edit
                    </Button>
                    <Button 
                      variant="outline-danger" 
                      size="sm"
                      onClick={() => handleDeleteLot(lot.id)}
                      disabled={lot.status === 'booked'}
                    >
                      <FontAwesomeIcon icon={faTrashAlt} className="me-1" />
                      Delete
                    </Button>
                  </div>
                </Card.Body>
              </Card>
            </Col>
          ))}
        </Row>
      )}
      
      {/* Add Lot Modal */}
      <Modal show={showAddModal} onHide={() => setShowAddModal(false)}>
        <Modal.Header closeButton>
          <Modal.Title>Add New Lot</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form>
            <Form.Group className="mb-3">
              <Form.Label>Lot Number*</Form.Label>
              <Form.Control
                type="text"
                name="lotNumber"
                value={formData.lotNumber}
                onChange={handleInputChange}
                placeholder="e.g., A1, B2, etc."
                required
              />
            </Form.Group>
            
            <Form.Group className="mb-3">
              <Form.Label>Location</Form.Label>
              <Form.Control
                type="text"
                name="location"
                value={formData.location}
                onChange={handleInputChange}
                placeholder="e.g., North Section, Main Area, etc."
              />
            </Form.Group>
            
            <Form.Group className="mb-3">
              <Form.Label>Size</Form.Label>
              <Form.Control
                type="text"
                name="size"
                value={formData.size}
                onChange={handleInputChange}
                placeholder="e.g., 3m x 5m, Standard, Large, etc."
              />
            </Form.Group>
            
            <Form.Group className="mb-3">
              <Form.Label>Price ($)</Form.Label>
              <Form.Control
                type="number"
                step="0.01"
                min="0"
                name="price"
                value={formData.price}
                onChange={handleInputChange}
                placeholder="Price for this lot"
              />
            </Form.Group>
            
            <Form.Group className="mb-3">
              <Form.Label>Status</Form.Label>
              <Form.Select 
                name="status"
                value={formData.status}
                onChange={handleInputChange}
              >
                <option value="available">Available</option>
                <option value="unavailable">Unavailable</option>
              </Form.Select>
            </Form.Group>
          </Form>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={() => setShowAddModal(false)}>
            Cancel
          </Button>
          <Button 
            variant="success" 
            onClick={handleAddLot}
            disabled={!formData.lotNumber}
          >
            Add Lot
          </Button>
        </Modal.Footer>
      </Modal>
      
      {/* Edit Lot Modal */}
      <Modal show={showEditModal} onHide={() => setShowEditModal(false)}>
        <Modal.Header closeButton>
          <Modal.Title>Edit Lot</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          {selectedLot && (
            <Form>
              <Form.Group className="mb-3">
                <Form.Label>Lot Number*</Form.Label>
                <Form.Control
                  type="text"
                  name="lotNumber"
                  value={formData.lotNumber}
                  onChange={handleInputChange}
                  placeholder="e.g., A1, B2, etc."
                  required
                />
              </Form.Group>
              
              <Form.Group className="mb-3">
                <Form.Label>Location</Form.Label>
                <Form.Control
                  type="text"
                  name="location"
                  value={formData.location}
                  onChange={handleInputChange}
                  placeholder="e.g., North Section, Main Area, etc."
                />
              </Form.Group>
              
              <Form.Group className="mb-3">
                <Form.Label>Size</Form.Label>
                <Form.Control
                  type="text"
                  name="size"
                  value={formData.size}
                  onChange={handleInputChange}
                  placeholder="e.g., 3m x 5m, Standard, Large, etc."
                />
              </Form.Group>
              
              <Form.Group className="mb-3">
                <Form.Label>Price ($)</Form.Label>
                <Form.Control
                  type="number"
                  step="0.01"
                  min="0"
                  name="price"
                  value={formData.price}
                  onChange={handleInputChange}
                  placeholder="Price for this lot"
                />
              </Form.Group>
              
              <Form.Group className="mb-3">
                <Form.Label>Status</Form.Label>
                <Form.Select 
                  name="status"
                  value={formData.status}
                  onChange={handleInputChange}
                  disabled={selectedLot.status === 'booked'}
                >
                  <option value="available">Available</option>
                  <option value="booked" disabled>Booked</option>
                  <option value="unavailable">Unavailable</option>
                </Form.Select>
                {selectedLot.status === 'booked' && (
                  <Form.Text className="text-muted">
                    Status cannot be changed while lot is booked.
                  </Form.Text>
                )}
              </Form.Group>
            </Form>
          )}
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={() => setShowEditModal(false)}>
            Cancel
          </Button>
          <Button 
            variant="primary" 
            onClick={handleEditLot}
            disabled={!formData.lotNumber}
          >
            Update Lot
          </Button>
        </Modal.Footer>
      </Modal>
      
      {/* Bulk Generate Modal */}
      <Modal show={showBulkModal} onHide={() => setShowBulkModal(false)}>
        <Modal.Header closeButton>
          <Modal.Title>Bulk Generate Lots</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form>
            <Form.Group className="mb-3">
              <Form.Label>Prefix*</Form.Label>
              <Form.Control
                type="text"
                name="prefix"
                value={bulkData.prefix}
                onChange={handleBulkInputChange}
                placeholder="e.g., A, B, Lot-, etc."
                required
              />
              <Form.Text className="text-muted">
                This will be combined with numbers to create lot numbers (e.g., A1, A2, etc.)
              </Form.Text>
            </Form.Group>
            
            <Row>
              <Col md={6}>
                <Form.Group className="mb-3">
                  <Form.Label>Start Number*</Form.Label>
                  <Form.Control
                    type="number"
                    min="1"
                    name="start"
                    value={bulkData.start}
                    onChange={handleBulkInputChange}
                    required
                  />
                </Form.Group>
              </Col>
              <Col md={6}>
                <Form.Group className="mb-3">
                  <Form.Label>End Number*</Form.Label>
                  <Form.Control
                    type="number"
                    min={bulkData.start}
                    name="end"
                    value={bulkData.end}
                    onChange={handleBulkInputChange}
                    required
                  />
                </Form.Group>
              </Col>
            </Row>
            
            <Form.Group className="mb-3">
              <Form.Label>Location</Form.Label>
              <Form.Control
                type="text"
                name="location"
                value={bulkData.location}
                onChange={handleBulkInputChange}
                placeholder="e.g., Section 1, Main Area, etc."
              />
            </Form.Group>
            
            <Form.Group className="mb-3">
              <Form.Label>Size</Form.Label>
              <Form.Control
                type="text"
                name="size"
                value={bulkData.size}
                onChange={handleBulkInputChange}
                placeholder="e.g., 3m x 5m, Standard, Large, etc."
              />
            </Form.Group>
            
            <Form.Group className="mb-3">
              <Form.Label>Price ($)</Form.Label>
              <Form.Control
                type="number"
                step="0.01"
                min="0"
                name="price"
                value={bulkData.price}
                onChange={handleBulkInputChange}
                placeholder="Price for these lots"
              />
            </Form.Group>
            
            <Alert variant="info">
              This will generate {Math.max(0, bulkData.end - bulkData.start + 1)} lot(s) with numbers from {bulkData.prefix}{bulkData.start} to {bulkData.prefix}{bulkData.end}.
            </Alert>
          </Form>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={() => setShowBulkModal(false)}>
            Cancel
          </Button>
          <Button 
            variant="success" 
            onClick={handleBulkGenerate}
            disabled={!bulkData.prefix || bulkData.end < bulkData.start}
          >
            Generate Lots
          </Button>
        </Modal.Footer>
      </Modal>
    </Container>
  );
};

export default ManageLots; 