import React, { useState, useContext } from 'react';
import { Container, Row, Col, Form, Button, Card, Alert } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCalendarAlt, faCar } from '@fortawesome/free-solid-svg-icons';
import DatePicker from 'react-datepicker';
import "react-datepicker/dist/react-datepicker.css";
import { toast } from 'react-toastify';
import axios from 'axios';
import AuthContext from '../../context/AuthContext';

const itemTypes = [
  'Clothing & Accessories',
  'Electronics',
  'Furniture',
  'Home Decor',
  'Toys & Games',
  'Books & Media',
  'Collectibles',
  'Garden & Outdoor',
  'Tools & DIY',
  'Sports Equipment',
  'Other'
];

const BookingForm = () => {
  const { user } = useContext(AuthContext);
  const navigate = useNavigate();
  const [error, setError] = useState(null);
  const [loading, setLoading] = useState(false);
  const [validated, setValidated] = useState(false);
  const [formData, setFormData] = useState({
    fullName: user ? user.fullName : '',
    phoneNumber: user ? user.phoneNumber : '',
    carPlate: '',
    address: user ? user.address : '',
    itemType: '',
    itemDescription: '',
    bookingDate: new Date()
  });

  const { fullName, phoneNumber, carPlate, address, itemType, itemDescription, bookingDate } = formData;

  const onChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const onDateChange = (date) => {
    setFormData({ ...formData, bookingDate: date });
  };

  const validateForm = () => {
    // Form validation logic
    const form = document.getElementById('booking-form');
    if (form.checkValidity() === false) {
      return false;
    }
    
    // Additional validations
    if (!bookingDate) {
      setError('Please select a booking date');
      return false;
    }
    
    if (bookingDate < new Date()) {
      setError('Booking date cannot be in the past');
      return false;
    }
    
    return true;
  };

  const onSubmit = async (e) => {
    e.preventDefault();
    setValidated(true);
    setError(null);
    
    if (validateForm()) {
      setLoading(true);
      try {
        const res = await axios.post('/api/bookings', {
          ...formData,
          bookingDate: bookingDate.toISOString().split('T')[0] // Format date as YYYY-MM-DD
        });
        
        toast.success('Booking submitted successfully! It is pending approval.');
        navigate('/my-bookings');
      } catch (err) {
        setError(
          err.response && err.response.data.message
            ? err.response.data.message
            : 'Something went wrong. Please try again.'
        );
        toast.error(
          err.response && err.response.data.message
            ? err.response.data.message
            : 'Something went wrong. Please try again.'
        );
      }
      setLoading(false);
    }
  };

  return (
    <Container className="py-5">
      <Row className="justify-content-center">
        <Col md={8}>
          <Card className="shadow-sm border-0">
            <Card.Body className="p-5">
              <div className="text-center mb-4">
                <FontAwesomeIcon icon={faCalendarAlt} size="3x" className="text-primary mb-3" />
                <h2>Book Your Car Boot Space</h2>
                <p className="text-muted">
                  Fill in the form below to book your spot at our next car boot sale event
                </p>
              </div>
              
              {error && <Alert variant="danger">{error}</Alert>}
              
              <Form noValidate validated={validated} onSubmit={onSubmit} id="booking-form">
                <Row>
                  <Col md={6}>
                    <Form.Group className="mb-3" controlId="fullName">
                      <Form.Label>Full Name</Form.Label>
                      <Form.Control
                        type="text"
                        name="fullName"
                        value={fullName}
                        onChange={onChange}
                        placeholder="Enter your full name"
                        required
                      />
                      <Form.Control.Feedback type="invalid">
                        Please provide your full name.
                      </Form.Control.Feedback>
                    </Form.Group>
                  </Col>
                  <Col md={6}>
                    <Form.Group className="mb-3" controlId="phoneNumber">
                      <Form.Label>Phone Number</Form.Label>
                      <Form.Control
                        type="text"
                        name="phoneNumber"
                        value={phoneNumber}
                        onChange={onChange}
                        placeholder="Enter your phone number"
                        required
                      />
                      <Form.Control.Feedback type="invalid">
                        Please provide a valid phone number.
                      </Form.Control.Feedback>
                    </Form.Group>
                  </Col>
                </Row>
                
                <Form.Group className="mb-3" controlId="carPlate">
                  <Form.Label>Car Registration Plate</Form.Label>
                  <Form.Control
                    type="text"
                    name="carPlate"
                    value={carPlate}
                    onChange={onChange}
                    placeholder="Enter your car registration plate"
                    required
                  />
                  <Form.Control.Feedback type="invalid">
                    Please provide your car registration plate.
                  </Form.Control.Feedback>
                </Form.Group>
                
                <Form.Group className="mb-3" controlId="address">
                  <Form.Label>Home Address</Form.Label>
                  <Form.Control
                    as="textarea"
                    rows={3}
                    name="address"
                    value={address}
                    onChange={onChange}
                    placeholder="Enter your home address"
                    required
                  />
                  <Form.Control.Feedback type="invalid">
                    Please provide your home address.
                  </Form.Control.Feedback>
                </Form.Group>
                
                <Row>
                  <Col md={6}>
                    <Form.Group className="mb-3" controlId="itemType">
                      <Form.Label>Type of Items to Sell</Form.Label>
                      <Form.Select
                        name="itemType"
                        value={itemType}
                        onChange={onChange}
                        required
                      >
                        <option value="">Select an item category</option>
                        {itemTypes.map((type, index) => (
                          <option key={index} value={type}>{type}</option>
                        ))}
                      </Form.Select>
                      <Form.Control.Feedback type="invalid">
                        Please select the type of items you'll be selling.
                      </Form.Control.Feedback>
                    </Form.Group>
                  </Col>
                  <Col md={6}>
                    <Form.Group className="mb-3" controlId="bookingDate">
                      <Form.Label>Preferred Date</Form.Label>
                      <div>
                        <DatePicker
                          selected={bookingDate}
                          onChange={onDateChange}
                          className="form-control"
                          dateFormat="dd/MM/yyyy"
                          minDate={new Date()}
                          placeholderText="Select your preferred date"
                          required
                        />
                      </div>
                      {!bookingDate && validated && (
                        <div className="text-danger small mt-1">
                          Please select a preferred date.
                        </div>
                      )}
                    </Form.Group>
                  </Col>
                </Row>
                
                <Form.Group className="mb-4" controlId="itemDescription">
                  <Form.Label>Item Description (Optional)</Form.Label>
                  <Form.Control
                    as="textarea"
                    rows={3}
                    name="itemDescription"
                    value={itemDescription}
                    onChange={onChange}
                    placeholder="Provide a brief description of the items you'll be selling"
                  />
                </Form.Group>
                
                <div className="d-grid gap-2 mt-4">
                  <Button variant="primary" type="submit" size="lg" disabled={loading}>
                    {loading ? 'Submitting...' : 'Submit Booking Request'}
                  </Button>
                  <Button variant="outline-secondary" onClick={() => navigate('/dashboard')}>
                    Cancel
                  </Button>
                </div>
              </Form>
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </Container>
  );
};

export default BookingForm; 