import React, { useContext } from 'react';
import { Navbar as BootstrapNavbar, Nav, Container, Button } from 'react-bootstrap';
import { Link, useNavigate } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faSignInAlt, faUserPlus, faSignOutAlt, faUser, faTachometerAlt, faCar } from '@fortawesome/free-solid-svg-icons';
import AuthContext from '../../context/AuthContext';

const Navbar = () => {
  const { isAuthenticated, user, logout } = useContext(AuthContext);
  const navigate = useNavigate();

  const onLogout = () => {
    logout();
    navigate('/');
  };

  const authLinks = (
    <Nav className="ms-auto">
      {user && user.role === 'admin' ? (
        <Nav.Link as={Link} to="/admin" className="nav-link">
          <FontAwesomeIcon icon={faTachometerAlt} className="me-1" />
          Admin Dashboard
        </Nav.Link>
      ) : (
        <Nav.Link as={Link} to="/dashboard" className="nav-link">
          <FontAwesomeIcon icon={faUser} className="me-1" />
          Dashboard
        </Nav.Link>
      )}
      <Nav.Link as={Link} to="/my-bookings" className="nav-link">
        <FontAwesomeIcon icon={faCar} className="me-1" />
        My Bookings
      </Nav.Link>
      <Button variant="outline-light" onClick={onLogout}>
        <FontAwesomeIcon icon={faSignOutAlt} className="me-1" />
        Logout
      </Button>
    </Nav>
  );

  const guestLinks = (
    <Nav className="ms-auto">
      <Nav.Link as={Link} to="/login" className="nav-link">
        <FontAwesomeIcon icon={faSignInAlt} className="me-1" />
        Login
      </Nav.Link>
      <Nav.Link as={Link} to="/register" className="nav-link">
        <FontAwesomeIcon icon={faUserPlus} className="me-1" />
        Register
      </Nav.Link>
    </Nav>
  );

  return (
    <BootstrapNavbar bg="primary" variant="dark" expand="lg" className="mb-4">
      <Container>
        <BootstrapNavbar.Brand as={Link} to="/">
          <img 
            src="/carbootlogo.png" 
            alt="CarBoot Sale" 
            width="30" 
            height="30" 
            className="d-inline-block align-top me-2"
            onError={(e) => {
              e.target.onerror = null;
              e.target.style.display = 'none';
            }}
          />
          CarBoot Sale Booking
        </BootstrapNavbar.Brand>
        <BootstrapNavbar.Toggle aria-controls="basic-navbar-nav" />
        <BootstrapNavbar.Collapse id="basic-navbar-nav">
          <Nav className="me-auto">
            <Nav.Link as={Link} to="/" className="nav-link">
              Home
            </Nav.Link>
            {isAuthenticated && (
              <Nav.Link as={Link} to="/booking" className="nav-link">
                Book a Space
              </Nav.Link>
            )}
          </Nav>
          {isAuthenticated ? authLinks : guestLinks}
        </BootstrapNavbar.Collapse>
      </Container>
    </BootstrapNavbar>
  );
};

export default Navbar; 