import React, { useContext, useEffect, useState } from 'react';
import { Container, Row, Col, Card, Button } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCalendarPlus, faListAlt, faUserCircle } from '@fortawesome/free-solid-svg-icons';
import AuthContext from '../../context/AuthContext';
import axios from 'axios';

const Dashboard = () => {
  const { user } = useContext(AuthContext);
  const [bookings, setBookings] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchBookings = async () => {
      try {
        const res = await axios.get('/api/bookings/user');
        setBookings(res.data);
        setLoading(false);
      } catch (err) {
        console.error('Error fetching bookings:', err);
        setLoading(false);
      }
    };

    fetchBookings();
  }, []);

  const getPendingBookingsCount = () => {
    return bookings.filter(booking => booking.status === 'pending').length;
  };

  const getApprovedBookingsCount = () => {
    return bookings.filter(booking => booking.status === 'approved').length;
  };

  return (
    <Container className="py-5">
      <Row className="mb-4">
        <Col>
          <h1 className="mb-3">
            <FontAwesomeIcon icon={faUserCircle} className="me-2" />
            Welcome, {user ? user.fullName : 'User'}
          </h1>
          <p className="lead text-muted">Manage your car boot sale bookings from here.</p>
        </Col>
      </Row>

      <Row className="mb-5">
        <Col md={4} className="mb-4 mb-md-0">
          <Card className="h-100 border-primary shadow-sm">
            <Card.Body className="text-center">
              <div className="display-4 text-primary mb-3">{bookings.length}</div>
              <Card.Title>Total Bookings</Card.Title>
              <Card.Text>All your booking requests</Card.Text>
            </Card.Body>
          </Card>
        </Col>
        <Col md={4} className="mb-4 mb-md-0">
          <Card className="h-100 border-warning shadow-sm">
            <Card.Body className="text-center">
              <div className="display-4 text-warning mb-3">{getPendingBookingsCount()}</div>
              <Card.Title>Pending Approval</Card.Title>
              <Card.Text>Bookings waiting for admin approval</Card.Text>
            </Card.Body>
          </Card>
        </Col>
        <Col md={4}>
          <Card className="h-100 border-success shadow-sm">
            <Card.Body className="text-center">
              <div className="display-4 text-success mb-3">{getApprovedBookingsCount()}</div>
              <Card.Title>Approved Bookings</Card.Title>
              <Card.Text>Your confirmed bookings</Card.Text>
            </Card.Body>
          </Card>
        </Col>
      </Row>

      <Row className="justify-content-center">
        <Col md={6} className="mb-4">
          <Card className="shadow-sm h-100">
            <Card.Body className="d-flex flex-column">
              <div className="text-center mb-4">
                <FontAwesomeIcon icon={faCalendarPlus} size="3x" className="text-primary mb-3" />
                <h3>Make a New Booking</h3>
                <p className="text-muted">
                  Reserve your spot at the next car boot sale event
                </p>
              </div>
              <div className="mt-auto">
                <div className="d-grid">
                  <Button as={Link} to="/booking" variant="primary" size="lg">
                    Book Now
                  </Button>
                </div>
              </div>
            </Card.Body>
          </Card>
        </Col>
        
        <Col md={6} className="mb-4">
          <Card className="shadow-sm h-100">
            <Card.Body className="d-flex flex-column">
              <div className="text-center mb-4">
                <FontAwesomeIcon icon={faListAlt} size="3x" className="text-primary mb-3" />
                <h3>View Your Bookings</h3>
                <p className="text-muted">
                  Check the status of your existing bookings
                </p>
              </div>
              <div className="mt-auto">
                <div className="d-grid">
                  <Button as={Link} to="/my-bookings" variant="outline-primary" size="lg">
                    My Bookings
                  </Button>
                </div>
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>

      {/* Recent Bookings Quick View */}
      {bookings.length > 0 && (
        <Row className="mt-5">
          <Col>
            <h3 className="mb-4">Recent Bookings</h3>
            <div className="table-responsive">
              <table className="table table-hover">
                <thead className="table-light">
                  <tr>
                    <th>Date</th>
                    <th>Item Type</th>
                    <th>Status</th>
                    <th>Lot Number</th>
                  </tr>
                </thead>
                <tbody>
                  {bookings.slice(0, 3).map(booking => (
                    <tr key={booking.id}>
                      <td>{new Date(booking.bookingDate).toLocaleDateString()}</td>
                      <td>{booking.itemType}</td>
                      <td>
                        <span className={`badge bg-${
                          booking.status === 'approved' 
                            ? 'success' 
                            : booking.status === 'rejected' 
                              ? 'danger' 
                              : 'warning'
                        }`}>
                          {booking.status.charAt(0).toUpperCase() + booking.status.slice(1)}
                        </span>
                      </td>
                      <td>{booking.lotNumber || 'Not assigned'}</td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
            <div className="text-center mt-3">
              <Button as={Link} to="/my-bookings" variant="link">
                View all bookings
              </Button>
            </div>
          </Col>
        </Row>
      )}
    </Container>
  );
};

export default Dashboard; 