import React, { useContext, useEffect, useState } from 'react';
import { Container, Row, Col, Card, Button, Badge } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faUsers, faCalendarCheck, faMapMarkerAlt, faClock, faCheck, faTimes } from '@fortawesome/free-solid-svg-icons';
import AuthContext from '../../context/AuthContext';
import axios from 'axios';

const AdminDashboard = () => {
  const { user } = useContext(AuthContext);
  const [bookings, setBookings] = useState([]);
  const [lots, setLots] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const [bookingsRes, lotsRes] = await Promise.all([
          axios.get('/api/bookings'),
          axios.get('/api/lots')
        ]);
        
        setBookings(bookingsRes.data);
        setLots(lotsRes.data);
        setLoading(false);
      } catch (err) {
        console.error('Error fetching data:', err);
        setLoading(false);
      }
    };

    fetchData();
  }, []);

  const getPendingBookingsCount = () => {
    return bookings.filter(booking => booking.status === 'pending').length;
  };

  const getApprovedBookingsCount = () => {
    return bookings.filter(booking => booking.status === 'approved').length;
  };

  const getAvailableLotsCount = () => {
    return lots.filter(lot => lot.status === 'available').length;
  };

  return (
    <Container className="py-5">
      <Row className="mb-4">
        <Col>
          <h1 className="mb-3">Admin Dashboard</h1>
          <p className="lead text-muted">
            Welcome back, {user ? user.fullName : 'Admin'}. Manage all car boot sale bookings and lots.
          </p>
        </Col>
      </Row>

      {/* Stats Cards */}
      <Row className="mb-5">
        <Col md={3} className="mb-4 mb-md-0">
          <Card className="h-100 border-primary shadow-sm">
            <Card.Body className="text-center">
              <div className="display-4 text-primary mb-3">{bookings.length}</div>
              <Card.Title>Total Bookings</Card.Title>
              <Card.Text>All booking requests</Card.Text>
            </Card.Body>
          </Card>
        </Col>
        
        <Col md={3} className="mb-4 mb-md-0">
          <Card className="h-100 border-warning shadow-sm">
            <Card.Body className="text-center">
              <div className="display-4 text-warning mb-3">{getPendingBookingsCount()}</div>
              <Card.Title>Pending Approval</Card.Title>
              <FontAwesomeIcon icon={faClock} className="text-warning" />
            </Card.Body>
          </Card>
        </Col>
        
        <Col md={3} className="mb-4 mb-md-0">
          <Card className="h-100 border-success shadow-sm">
            <Card.Body className="text-center">
              <div className="display-4 text-success mb-3">{getApprovedBookingsCount()}</div>
              <Card.Title>Approved Bookings</Card.Title>
              <FontAwesomeIcon icon={faCheck} className="text-success" />
            </Card.Body>
          </Card>
        </Col>
        
        <Col md={3}>
          <Card className="h-100 border-info shadow-sm">
            <Card.Body className="text-center">
              <div className="display-4 text-info mb-3">{getAvailableLotsCount()}</div>
              <Card.Title>Available Lots</Card.Title>
              <FontAwesomeIcon icon={faMapMarkerAlt} className="text-info" />
            </Card.Body>
          </Card>
        </Col>
      </Row>

      {/* Quick Actions */}
      <Row className="justify-content-center mb-5">
        <Col md={4} className="mb-4">
          <Card className="h-100 shadow-sm">
            <Card.Body className="d-flex flex-column">
              <div className="text-center mb-4">
                <FontAwesomeIcon icon={faCalendarCheck} size="3x" className="text-primary mb-3" />
                <h3>Manage Bookings</h3>
                <p className="text-muted">
                  Review, approve, or reject booking requests
                </p>
              </div>
              <div className="mt-auto">
                <div className="d-grid">
                  <Button as={Link} to="/admin/bookings" variant="primary" size="lg">
                    View Bookings {getPendingBookingsCount() > 0 && <Badge variant="light">{getPendingBookingsCount()}</Badge>}
                  </Button>
                </div>
              </div>
            </Card.Body>
          </Card>
        </Col>
        
        <Col md={4} className="mb-4">
          <Card className="h-100 shadow-sm">
            <Card.Body className="d-flex flex-column">
              <div className="text-center mb-4">
                <FontAwesomeIcon icon={faMapMarkerAlt} size="3x" className="text-primary mb-3" />
                <h3>Manage Lots</h3>
                <p className="text-muted">
                  Configure available lots and locations
                </p>
              </div>
              <div className="mt-auto">
                <div className="d-grid">
                  <Button as={Link} to="/admin/lots" variant="primary" size="lg">
                    Manage Lots
                  </Button>
                </div>
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>

      {/* Recent Bookings */}
      {bookings.length > 0 && (
        <Row>
          <Col>
            <h3 className="mb-4">Recent Bookings</h3>
            <div className="table-responsive">
              <table className="table table-hover">
                <thead className="table-light">
                  <tr>
                    <th>ID</th>
                    <th>User</th>
                    <th>Date</th>
                    <th>Item Type</th>
                    <th>Status</th>
                    <th>Lot Number</th>
                  </tr>
                </thead>
                <tbody>
                  {bookings.slice(0, 5).map(booking => (
                    <tr key={booking.id}>
                      <td>{booking.id}</td>
                      <td>{booking.user ? booking.user.fullName : booking.fullName}</td>
                      <td>{new Date(booking.bookingDate).toLocaleDateString()}</td>
                      <td>{booking.itemType}</td>
                      <td>
                        <span className={`badge bg-${
                          booking.status === 'approved' 
                            ? 'success' 
                            : booking.status === 'rejected' 
                              ? 'danger' 
                              : 'warning'
                        }`}>
                          {booking.status.charAt(0).toUpperCase() + booking.status.slice(1)}
                        </span>
                      </td>
                      <td>{booking.lotNumber || 'Not assigned'}</td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
            <div className="text-center mt-3">
              <Button as={Link} to="/admin/bookings" variant="link">
                View all bookings
              </Button>
            </div>
          </Col>
        </Row>
      )}
    </Container>
  );
};

export default AdminDashboard; 