import React, { useState, useEffect } from 'react';
import { Container, Row, Col, Card, Button, Badge, Modal, Alert } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCalendarCheck, faTrashAlt, faInfoCircle, faExclamationTriangle } from '@fortawesome/free-solid-svg-icons';
import { toast } from 'react-toastify';
import axios from 'axios';

const UserBookings = () => {
  const [bookings, setBookings] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [showDeleteModal, setShowDeleteModal] = useState(false);
  const [bookingToDelete, setBookingToDelete] = useState(null);
  const [showDetailsModal, setShowDetailsModal] = useState(false);
  const [selectedBooking, setSelectedBooking] = useState(null);

  useEffect(() => {
    const fetchBookings = async () => {
      try {
        const res = await axios.get('/api/bookings/user');
        setBookings(res.data);
        setLoading(false);
      } catch (err) {
        setError('Failed to load bookings. Please try again later.');
        console.error('Error fetching bookings:', err);
        setLoading(false);
      }
    };

    fetchBookings();
  }, []);

  const handleDelete = async () => {
    if (!bookingToDelete) return;

    try {
      await axios.delete(`/api/bookings/${bookingToDelete}`);
      setBookings(bookings.filter(booking => booking.id !== bookingToDelete));
      toast.success('Booking successfully deleted');
      setShowDeleteModal(false);
      setBookingToDelete(null);
    } catch (err) {
      toast.error(
        err.response && err.response.data.message
          ? err.response.data.message
          : 'Failed to delete booking'
      );
    }
  };

  const confirmDelete = (id) => {
    setBookingToDelete(id);
    setShowDeleteModal(true);
  };

  const showDetails = (booking) => {
    setSelectedBooking(booking);
    setShowDetailsModal(true);
  };

  const getStatusBadge = (status) => {
    let variant;
    switch (status) {
      case 'approved':
        variant = 'success';
        break;
      case 'rejected':
        variant = 'danger';
        break;
      default:
        variant = 'warning';
    }
    return (
      <Badge bg={variant}>
        {status.charAt(0).toUpperCase() + status.slice(1)}
      </Badge>
    );
  };

  if (loading) {
    return (
      <Container className="py-5 text-center">
        <div className="spinner-border text-primary" role="status">
          <span className="visually-hidden">Loading...</span>
        </div>
        <p className="mt-3">Loading your bookings...</p>
      </Container>
    );
  }

  if (error) {
    return (
      <Container className="py-5">
        <Alert variant="danger">
          <FontAwesomeIcon icon={faExclamationTriangle} className="me-2" />
          {error}
        </Alert>
      </Container>
    );
  }

  return (
    <Container className="py-5">
      <div className="d-flex justify-content-between align-items-center mb-4">
        <h2>
          <FontAwesomeIcon icon={faCalendarCheck} className="me-2" />
          My Bookings
        </h2>
        <Button as={Link} to="/booking" variant="primary">
          New Booking
        </Button>
      </div>

      {bookings.length === 0 ? (
        <Card className="text-center p-5 shadow-sm">
          <Card.Body>
            <h4>You don't have any bookings yet</h4>
            <p className="text-muted">
              Book your spot at our next car boot sale event!
            </p>
            <Button as={Link} to="/booking" variant="primary" size="lg" className="mt-3">
              Book Now
            </Button>
          </Card.Body>
        </Card>
      ) : (
        <Row>
          {bookings.map(booking => (
            <Col md={6} lg={4} className="mb-4" key={booking.id}>
              <Card className="h-100 shadow-sm">
                <Card.Header className="d-flex justify-content-between align-items-center">
                  <h5 className="mb-0">Booking #{booking.id}</h5>
                  {getStatusBadge(booking.status)}
                </Card.Header>
                <Card.Body>
                  <Card.Title>{booking.itemType}</Card.Title>
                  <Card.Text>
                    <strong>Date:</strong> {new Date(booking.bookingDate).toLocaleDateString()}
                    <br />
                    <strong>Lot:</strong> {booking.lotNumber || 'Not assigned yet'}
                  </Card.Text>
                  <div className="d-flex justify-content-between mt-3">
                    <Button 
                      variant="outline-info" 
                      size="sm"
                      onClick={() => showDetails(booking)}
                    >
                      <FontAwesomeIcon icon={faInfoCircle} className="me-1" />
                      Details
                    </Button>
                    <Button 
                      variant="outline-danger" 
                      size="sm"
                      onClick={() => confirmDelete(booking.id)}
                      disabled={booking.status === 'approved'}
                    >
                      <FontAwesomeIcon icon={faTrashAlt} className="me-1" />
                      Cancel
                    </Button>
                  </div>
                </Card.Body>
                <Card.Footer className="text-muted">
                  Created: {new Date(booking.createdAt).toLocaleDateString()}
                </Card.Footer>
              </Card>
            </Col>
          ))}
        </Row>
      )}

      {/* Delete Confirmation Modal */}
      <Modal show={showDeleteModal} onHide={() => setShowDeleteModal(false)}>
        <Modal.Header closeButton>
          <Modal.Title>Confirm Cancellation</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          Are you sure you want to cancel this booking? This action cannot be undone.
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={() => setShowDeleteModal(false)}>
            No, Keep It
          </Button>
          <Button variant="danger" onClick={handleDelete}>
            Yes, Cancel Booking
          </Button>
        </Modal.Footer>
      </Modal>

      {/* Booking Details Modal */}
      <Modal 
        show={showDetailsModal} 
        onHide={() => setShowDetailsModal(false)}
        size="lg"
      >
        <Modal.Header closeButton>
          <Modal.Title>Booking Details</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          {selectedBooking && (
            <div>
              <Row className="mb-3">
                <Col sm={4} className="text-muted">Booking ID:</Col>
                <Col sm={8}>{selectedBooking.id}</Col>
              </Row>
              <Row className="mb-3">
                <Col sm={4} className="text-muted">Status:</Col>
                <Col sm={8}>{getStatusBadge(selectedBooking.status)}</Col>
              </Row>
              <Row className="mb-3">
                <Col sm={4} className="text-muted">Date:</Col>
                <Col sm={8}>{new Date(selectedBooking.bookingDate).toLocaleDateString()}</Col>
              </Row>
              <Row className="mb-3">
                <Col sm={4} className="text-muted">Full Name:</Col>
                <Col sm={8}>{selectedBooking.fullName}</Col>
              </Row>
              <Row className="mb-3">
                <Col sm={4} className="text-muted">Phone Number:</Col>
                <Col sm={8}>{selectedBooking.phoneNumber}</Col>
              </Row>
              <Row className="mb-3">
                <Col sm={4} className="text-muted">Car Plate:</Col>
                <Col sm={8}>{selectedBooking.carPlate}</Col>
              </Row>
              <Row className="mb-3">
                <Col sm={4} className="text-muted">Address:</Col>
                <Col sm={8}>{selectedBooking.address}</Col>
              </Row>
              <Row className="mb-3">
                <Col sm={4} className="text-muted">Item Type:</Col>
                <Col sm={8}>{selectedBooking.itemType}</Col>
              </Row>
              {selectedBooking.itemDescription && (
                <Row className="mb-3">
                  <Col sm={4} className="text-muted">Item Description:</Col>
                  <Col sm={8}>{selectedBooking.itemDescription}</Col>
                </Row>
              )}
              <Row className="mb-3">
                <Col sm={4} className="text-muted">Lot Number:</Col>
                <Col sm={8}>{selectedBooking.lotNumber || 'Not assigned yet'}</Col>
              </Row>
              <Row className="mb-3">
                <Col sm={4} className="text-muted">Created:</Col>
                <Col sm={8}>{new Date(selectedBooking.createdAt).toLocaleString()}</Col>
              </Row>
              <Row className="mb-3">
                <Col sm={4} className="text-muted">Last Updated:</Col>
                <Col sm={8}>{new Date(selectedBooking.updatedAt).toLocaleString()}</Col>
              </Row>
            </div>
          )}
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={() => setShowDetailsModal(false)}>
            Close
          </Button>
        </Modal.Footer>
      </Modal>
    </Container>
  );
};

export default UserBookings; 