import React from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import './App.css';

// Auth Components
import Login from './components/auth/Login';
import Register from './components/auth/Register';
import PrivateRoute from './components/routing/PrivateRoute';
import AdminRoute from './components/routing/AdminRoute';

// Layout Components
import Navbar from './components/layout/Navbar';
import Home from './components/layout/Home';
import Footer from './components/layout/Footer';

// User Components
import Dashboard from './components/user/Dashboard';
import BookingForm from './components/user/BookingForm';
import UserBookings from './components/user/UserBookings';

// Admin Components
import AdminDashboard from './components/admin/AdminDashboard';
import ManageBookings from './components/admin/ManageBookings';
import ManageLots from './components/admin/ManageLots';

// Auth Provider
import { AuthProvider } from './context/AuthContext';

function App() {
  return (
    <AuthProvider>
      <Router>
        <div className="app-container">
          <Navbar />
          <main className="main-content">
            <Routes>
              {/* Public Routes */}
              <Route path="/" element={<Home />} />
              <Route path="/login" element={<Login />} />
              <Route path="/register" element={<Register />} />
              
              {/* Protected User Routes */}
              <Route path="/dashboard" element={<PrivateRoute><Dashboard /></PrivateRoute>} />
              <Route path="/booking" element={<PrivateRoute><BookingForm /></PrivateRoute>} />
              <Route path="/my-bookings" element={<PrivateRoute><UserBookings /></PrivateRoute>} />
              
              {/* Protected Admin Routes */}
              <Route path="/admin" element={<AdminRoute><AdminDashboard /></AdminRoute>} />
              <Route path="/admin/bookings" element={<AdminRoute><ManageBookings /></AdminRoute>} />
              <Route path="/admin/lots" element={<AdminRoute><ManageLots /></AdminRoute>} />
              
              {/* Fallback Route */}
              <Route path="*" element={<Navigate to="/" />} />
            </Routes>
          </main>
          <Footer />
          <ToastContainer position="bottom-right" />
        </div>
      </Router>
    </AuthProvider>
  );
}

export default App;
