import React from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import { Link } from 'react-router-dom';

const Footer = () => {
  const currentYear = new Date().getFullYear();

  return (
    <footer className="bg-dark text-white py-4 mt-auto">
      <Container>
        <Row>
          <Col md={4} className="mb-3 mb-md-0">
            <h5>CarBoot Sale Booking</h5>
            <p className="text-muted">
              Book your spot at our next car boot sale event easily and quickly.
            </p>
          </Col>
          <Col md={4} className="mb-3 mb-md-0">
            <h5>Quick Links</h5>
            <ul className="list-unstyled">
              <li>
                <Link to="/" className="text-muted">Home</Link>
              </li>
              <li>
                <Link to="/login" className="text-muted">Login</Link>
              </li>
              <li>
                <Link to="/register" className="text-muted">Register</Link>
              </li>
              <li>
                <Link to="/booking" className="text-muted">Book a Space</Link>
              </li>
            </ul>
          </Col>
          <Col md={4}>
            <h5>Contact Us</h5>
            <address className="text-muted">
              <p>123 Market Street</p>
              <p>City Center</p>
              <p>Email: contact@carbootsale.com</p>
              <p>Phone: (123) 456-7890</p>
            </address>
          </Col>
        </Row>
        <hr />
        <div className="text-center">
          <p className="mb-0">
            &copy; {currentYear} CarBoot Sale Booking System. All rights reserved.
          </p>
        </div>
      </Container>
    </footer>
  );
};

export default Footer; 